@import url(https://fonts.googleapis.com/css?family=Monoton);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

*{font-family:"HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;font-weight:300}body{background:linear-gradient(to top left, #B9b9b9, #fff)}html{font-size:16px}@media only screen and (min-device-width: 375px) and (max-device-width: 812px){html{font-size:12px}}@media only screen and (max-device-width: 375px){html{font-size:8px}.card .card__cta .card-wrapper p.website,.card .card__cont .profession,.card .card__cont .other-information{font-size:8px}.card .card__cont .card-name{font-size:12px}}.blue{color:#29b6f6}.green{color:#9ccc65}.purple{color:#ba68c8}.cyan{color:#4dd0e1}.red{color:#ef5350}.content{height:100vh;color:#000;display:flex;justify-content:center}.uppercase{text-transform:uppercase}.card{perspective:150rem;height:15rem;width:30rem;margin-top:25%;display:flex;align-items:center;justify-content:center;position:relative;background-color:transparent}.card .card-wrapper{background-color:#fff;height:14rem;position:relative;width:29rem}.card__side{height:100%;transition:all 0.8s ease;background-color:#fff;position:absolute;top:0;left:0;margin:auto;width:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;border-radius:3px;overflow:hidden;box-shadow:0 1.5rem 4rem rgba(0,0,0,0.4)}.card__side--front .card-wrapper .card-name{margin-top:5.5rem}.card__side--back{transform:rotateY(180deg)}.card:hover .card__side--front{transform:rotateY(-180deg)}.card:hover .card__side--back{transform:rotateY(0deg)}.card__cont{height:15rem;background-image:linear-gradient(to top left, #9b8712, #C4AB14);display:flex;flex-direction:column;align-items:center;justify-content:center}.card__cont>p{margin:4px 0}.card__cont .card-name{font-size:18px;font-weight:400;margin-bottom:0.5rem}.card__cont .profession{font-size:11px;font-weight:100;margin:4px 0}.card__cont .other-information{display:flex;flex-direction:row;font-size:11px;position:absolute;justify-content:space-between;width:27.5rem;bottom:0;padding:.5rem .75rem}.card__cont .other-information .right-side .address{margin-left:15px}.card__cont .other-information .right-side .address svg{position:absolute;margin-left:-16px;margin-top:14px}.card__cont .other-information>div{display:flex;flex-direction:column;justify-content:flex-start}.card__cont .other-information>div>span{text-align:left;display:flex;align-items:center}.card__cont .other-information>div>span.city{margin-left:14px}.card__cont .other-information>div>span>svg{margin-right:4px}.card__cta{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#9b8712;height:15rem;width:30rem;background-image:linear-gradient(to top left, #9b8712, #C4AB14);display:flex;flex-direction:column;align-items:center;justify-content:center}.card__cta .card-wrapper{display:flex;flex-direction:row;justify-content:center;align-items:center}.card__cta .card-wrapper p.website{position:absolute;bottom:0;margin:4px 0;width:100%;text-align:center;font-size:13px}.card__cta .card-wrapper span{font-family:"Monoton", Helvetica, sans-serif;font-size:3rem;font-weight:600;color:transparent;background:linear-gradient(to top left, #9b8712, #F3D867);-webkit-background-clip:text}.card__cta .card-wrapper span.second-initial{margin-top:1rem;margin-left:-.25rem}.card__cta .card-wrapper span.first-inital{margin-bottom:1rem}.card__cta p{margin-left:1rem}.card__cta p>.space{margin-left:2rem}

